import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    component: {
      type: Object
    },
    onDragstart: {
      type: Function
    },
    onDragend: {
      type: Function
    }
  },
  setup (props) {
    return () => (
      <>
        <div
          class="component-aside__row__components__item mt-10px flex flex-col"
        >
        <div class="component-aside__row__components__item__label">
          {props.component.label}
        </div>
        <div
          class="component-aside__row__components__item__component"
          draggable
          onDragstart={(e) => props.DragFunction(e, props.component)}
          onDragend={(e) => props.DragEndFunction(e, props.component)}
        >
          {props.component.preview()}
        </div>
        </div>
      </>
    )
  }
})
