import { defineComponent, ref } from 'vue'
import { ElSelect, ElOption, ElCol } from 'element-plus'
export default defineComponent({
  setup () {
    function changeTheme (themeName) {
      // 移除所有主题类
      const classList = ['theme-default', 'theme-mint', 'theme-dark', 'theme-candy']
      classList.forEach(cls => {
        window.document.documentElement.classList.remove(cls)
      })
      // 添加新的主题类
      window.document.documentElement.classList.add(themeName)
    }
    // 皮肤主题
    const themesOption = [
      { value: 'theme-default', label: '默认主题' },
      { value: 'theme-mint', label: '薄荷主题' },
      { value: 'theme-dark', label: '暗色主题' },
      { value: 'theme-candy', label: '柔和主题' }
    ]
    const currentTheme = ref('theme-default')
    return () => {
      return (
        <ElCol span={4} offset={20}>
          <ElSelect
            v-model={currentTheme.value}
            onChange={changeTheme}
          >
            {
              themesOption.map((theme, index) => (
                <ElOption
                  key={index}
                  value={theme.value}
                  label={theme.label}
                   disabled={theme.value === currentTheme.value}
                ></ElOption>
              ))
            }
          </ElSelect>
        </ElCol>
      )
    }
  }
})
